import { Injectable } from '@angular/core';

import {HttpClient,HttpErrorResponse} from '@angular/common/Http';

//IMPORTING OBSERVABLE RELATED CODE
import { Observable } from 'rxjs/observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/do';


@Injectable({
  providedIn: 'root'
})
export class BooksHttpsService {
  private baseUrl="https://www.anapioficeandfire.com/api/";

  constructor(private _https:HttpClient) { 
    console.log("Service of booksHttpsService is called");
  }

  public getAllBookDetails():any{
    let myResponse =this._https.get(this.baseUrl + 'books');
    return myResponse;
  } 
  
  //getAllCharacterDetails end
  public getAllCharacterDetails():any{
      let myResponse =this._https.get(this.baseUrl + 'characters');
      return myResponse;
      
    } 
    //getAllCharacterDetails end

    //getAllHouse Details end
  public getAllHouseDetails():any{
    let myResponse =this._https.get(this.baseUrl + 'houses');
    return myResponse;
    
  } //getAllBookDetails end
  
  public getSingleBookInfo(bookId): any{
    let myResponse1 = this._https.get(this.baseUrl + '/books' + '/' + bookId)
    return myResponse1;
  }//end get single Book info

  public getSingleCharacterInfo(characterId): any{
    let myResponse2 = this._https.get(this.baseUrl + '/characters' + '/' + characterId)
    return myResponse2;
  }//end get single character info
  
  public getSingleHouseInfo(houseId): any{
    let myResponse3 = this._https.get(this.baseUrl + '/houses' + '/' + houseId)
    return myResponse3;
  }//end get single house info

}// booksHttpsService class end here
