import { Component, OnInit, OnDestroy } from '@angular/core';
import {HttpClient,HttpErrorResponse} from '@angular/common/Http';
//activate route is import
import { ActivatedRoute, Router } from '@angular/router';
// HTTP SERVICE IMPORTING
import {BooksHttpsService} from '../books-https.service';
//importing location to locate the location of back
import {Location} from '@angular/common';

@Component({
  selector: 'app-view-single-item',
  templateUrl: './view-single-item.component.html',
  styleUrls: ['./view-single-item.component.css']
})
export class ViewSingleItemComponent implements OnInit, OnDestroy {
  public currentBook;
  public currentCharacter;
  public currentHouse;

  constructor(private _route: ActivatedRoute, private router: Router, public booksHttpsService: BooksHttpsService) { 
    console.log("constructor view single item called");
  }

  ngOnInit() {
    console.log("OnInit view single item called");
   
    //for books
    let bookId = this._route.snapshot.paramMap.get('bookId');
    console.log(bookId);
    this.booksHttpsService.getSingleBookInfo(bookId).subscribe(

      data => {
        console.log("Books data");
        console.log(data);
        this.currentBook = data;
        for(let item in this.currentBook){
          if(this.currentBook[item]==""){
            this.currentBook[item]="N/A"
          }
        }
      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage)
      }
    )

    // for house
    let houseId = this._route.snapshot.paramMap.get('houseId');
    console.log(houseId);
    this.booksHttpsService.getSingleHouseInfo(houseId).subscribe(

      data => {
        console.log("currentHouse data");
        console.log(data);
        this.currentHouse = data;
        for(let item in this.currentBook){
          if(this.currentHouse[item]==""){
            this.currentHouse[item]="N/A"
          }
        }
      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage)
      }
    )

    // for charater
    let characterId = this._route.snapshot.paramMap.get('characterId');
    console.log(characterId);
    this.booksHttpsService.getSingleCharacterInfo(characterId).subscribe(

      data => {
        console.log("currentCharacter data");
        console.log(data);
        this.currentCharacter = data;
        for(let item in this.currentBook){
          if(this.currentCharacter[item]==""){
            this.currentCharacter[item]="N/A"
          }
        }
      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage)
      }
    )
  }

  ngOnDestroy(){
    console.log("Destory view single item called");
  }

}
