import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

// ROUTER
import {RouterModule, Routes} from '@angular/router';

// HTTP SERVICE IMPORTING
import {BooksHttpsService} from './books-https.service';

// EXTRA IMPORTING 
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';

// ALL CUSTOM COMPONENTS 
import { AppComponent } from './app.component';
import { ViewBooksComponent } from './view-books/view-books.component';
import { NotFoundComponent } from './not-found/not-found.component';
import { ViewSingleItemComponent } from './view-single-item/view-single-item.component';

@NgModule({
  declarations: [
    AppComponent,
    ViewBooksComponent,
    NotFoundComponent,
    ViewSingleItemComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    HttpClientModule,
    // RouterModule forRoot method to declare the possible routes in application
    RouterModule.forRoot([
      { path: '', redirectTo: 'allItems', pathMatch: 'full' },
      {path:'allItems', component: ViewBooksComponent},
      {path:'singleBookDetails', component: ViewBooksComponent},
      {path:'notFound', component: NotFoundComponent},
      {path:'books/:bookId',component:ViewSingleItemComponent},
      {path:'houses/:houseId',component:ViewSingleItemComponent},
      {path:'characters/:characterId',component:ViewSingleItemComponent},
    ])
  ],
  providers: [BooksHttpsService],
  bootstrap: [AppComponent]
})
export class AppModule { }
