import { Component, OnInit, OnDestroy } from '@angular/core';
// HTTPS REQUEST
import {HttpClient,HttpErrorResponse} from '@angular/common/Http';
// HTTP SERVICE IMPORTING
import {BooksHttpsService} from '../books-https.service';
@Component({
  selector: 'app-view-books',
  templateUrl: './view-books.component.html',
  styleUrls: ['./view-books.component.css']
})
export class ViewBooksComponent implements OnInit, OnDestroy {
  private allBooks =[];
  private allcharacter =[];
  private allHouses=[];
  private allItemDetails =[];

  constructor(private booksHttpsService:BooksHttpsService) {
    console.log("Constructor view books called");
   }

  ngOnInit() {
    console.log("OnInit view books called");
   //this is for book
    this.allBooks = this.booksHttpsService.getAllBookDetails().subscribe(
      data => {
        console.log(data);
        this.allBooks = data;
        this.allBooks.sort((a, b) => a.name.localeCompare(b.name)); //to sort books alphabetically
        this.allItemDetails = this.allItemDetails.concat(this.allBooks);
        //console.log(this.allItemDetails);
      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage)
      }
    )
    console.log("the book"+this.allBooks);

    //this is for Character
    this.allBooks = this.booksHttpsService.getAllCharacterDetails().subscribe(
      data => {
        console.log(data);
        this.allcharacter = data;
        this.allcharacter.sort((a, b) => a.name.localeCompare(b.name)); //to sort books alphabetically
        this.allItemDetails = this.allItemDetails.concat(this.allcharacter);
        for(let item of this.allItemDetails) {
          if(item.name==""){
            item.name="N/A"
          }
        }
        //console.log(this.allItemDetails);
      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage)
      }
    )
    //console.log("the character"+this.allcharacter);

 //this is for Houses
    this.allBooks = this.booksHttpsService.getAllHouseDetails().subscribe(
      data => {
        console.log(data);
        this.allHouses = data;
        this.allHouses.sort((a, b) => a.name.localeCompare(b.name)); //to sort books alphabetically
        this.allItemDetails = this.allItemDetails.concat(this.allHouses);
        for(let item of this.allItemDetails){
          if(item.titles == ''){
            item.titles='N/A';
          }
        }
        console.log(this.allItemDetails);

      },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage)
      }
    )
    console.log("the Houses"+this.allHouses);
    
  }//onInit fuction end

  ngOnDestroy(){
    console.log("Destroy view books called");
  }

}
